<template>
	<el-main>
		<h2>v-text</h2>
		<h2>v-html</h2>
		<h2>v-show</h2>
		<h2>v-if</h2>
		<h2>v-else</h2>
		<h2>v-else-if</h2>
		<h2>v-for</h2>
		<h2>v-on</h2>
		<h2>v-bind</h2>
		<h2>v-model</h2>
		<h2>v-pre</h2>
		<h2>v-cloak</h2>
		<h2>v-once</h2>
		
		<ul>
			<li v-for="item in list">
				<span>{{item.email}}</span>
				{{item}}</li>
		</ul>
		<el-button type='success' @click="addUser">新增</el-button>
	</el-main>
</template>

<script>
export default{
	data(){
		return{
			list:[],
		}
	},
	created(){
		this.getUser()
		console.log("在模板渲染成html前调用，即通常初始化某些属性值，然后再渲染成视图。")
	},
	mounted(){
		console.log("在模板渲染成html后调用，通常是初始化页面完成后，再对html的dom节点进行一些需要的操作。")
	},
	methods:{
		getUser(){
			this.$Apis.ajaxGetFetch('https://wd6783237698uedlow.wilddogio.com/users.json')
				.then((data)=>{
					console.log(data)
					this.list = data
				})
				
		},
		addUser(){
			this.$Apis.ajaxPostFetch('https://wd6783237698uedlow.wilddogio.com/users.json',{
				email:'13260094140@163.com',
				password:'123456',
				passwordCommit:'123456'
			}).then((data)=>{
				console.log(data)
				this.getUser()
			})
		}
	}
}
</script>

<style>
</style>


